import { isUniFrameWork } from '../../utils/env';
interface IProps {
  message: IMessageModel;
  showStyle: string;
  bubbleClassNameList?: string[];
  blinkMessageIDList?: string[];
}
const __sfc_main = {};
__sfc_main.props = {
